.footer-container {
  display: flex;
  align-items: center;
  gap: 30px;
  background-color: $main-color;

  @include desk {
    display: flex;
    align-items: center;
    padding: 13px 75px 23px 65px;
  }
}

.contacts-list {
  @include desk {
    margin-left: auto;
  }
  &__link {
    font-size: 14px;
    line-height: 24px;
    text-align: left;
    color: rgba(107, 107, 107, 1);
  }
}
